
@media (max-width: 600px){
    .personInfoView{
        color : rgb(174, 186, 192);
        background-color: #444;
        position: absolute; top: 0; left: 0;
        border: 3px black solid;
        border-radius: 8px;
        min-width: 200px;
        display: block;
    }

    .personInfoView.initial{
        margin: 0 auto;
        margin-top: 5vh;
        height: 10vh;
        min-width: 200px;
        width: 20vw;
        transform: translate(calc(50vw - 50%), 0px) scale(0,0);
    }

    .personInfoView.complete{
        height: 90vh;
        margin: 5px 5px 5px 0px;
        margin-top: 5vh;
        height: 90vh;
        min-width: 400px;
        width: 70vw;
        transform: translate(calc(95vw - 100%), 0px);
    }
}