.listItemView {
    overflow: auto;
    max-height: calc(90vh);
    transition: height, max-height 500ms linear;
}

.listItemView.hidden {
    max-height: 0px;
}

.listItem, .listItem>* {
    background-color: rgb(29, 86, 161);
}

.listItem {
    color : rgb(213, 224, 230);
    border-radius: 2px;
    box-shadow: 0px 10px 13px -10px rgba(0, 0, 0, 0.8);
    padding: 8px 10px 15px 20px;
    margin: 4px 5px 5px 5px;
}

::-webkit-scrollbar{
    width: 4px;
    background: 0;
}

::-webkit-scrollbar-thumb {
    background: rgb(29, 86, 161);
    border-radius: 2px;
    box-shadow: 0px 10px 13px -10px rgba(0, 0, 0, 0.8);
}


@media (min-width: 600px){



    .listItem * {
        margin: 0;
    }

    .listItemView, .listItemView.hidden {
        position: absolute; top: 15%; left: 0;
        margin: 0 auto;
        max-height: 85%;
        min-width: 200px;
        width: 20vw;
        display: block;

        overflow: auto;
    }

    ::-webkit-scrollbar{
        width: 10px;
    }
}