html, body{
    background: rgb(148, 189, 226);
}
#root{
    width: 100vw;
    height: 100vh;
}
header {
    background-color: rgb(39, 52, 82);
    display: flex;
    width: 100vw;
    flex-direction: column;
}
.title {
    --f: calc(1/100);
    text-align: center;
    color: #FFFFFF;
    font-size: 2.5em;
    text-shadow: 0 calc(var(--f) * 1em) 0 #CCCCCC, 0 calc(var(--f) * 2em) 0 #c9c9c9, 
        0 calc(var(--f) * 3em) 0 #bbb, 0 calc(var(--f) * 4em) 0 #b9b9b9, 0 calc(var(--f) * 5em) 0 #aaa, 
        0 calc(var(--f) * 6em) 1px rgba(0,0,0,.1), 0 0 5px rgba(0,0,0,.1), 
        0 calc(var(--f) * 1em) 3px rgba(0,0,0,.3), 0 calc(var(--f) * 3em) 5px rgba(0,0,0,.2), 
        0 calc(var(--f) * 5em) 10px rgba(0,0,0,.25), 0 calc(var(--f) * 10em) 10px rgba(0,0,0,.2), 
        0 calc(var(--f) * 20em) 20px rgba(0,0,0,.15);
}
.menuButton {
    display: inline-block;
    width: 100%;
    background-color: rgb(39, 52, 82);
    height: 5vh;
    text-align: center;
    color: rgb(179, 199, 216);
    border:0px solid black;
}

@media (min-width: 600px){
    header{
        height: 15%;
        max-height: 15%;
    }
    
    .title {
        text-align: left;
        margin: 0 0 0 14px;
        padding: 0;
        font-size: 5em;
    }
    .menuButton {
        display: none;
    }

}

